<script>
import $ from 'jquery';
import NavBar2 from '@/components/NavBar2.vue';
import { useRoute } from 'vue-router';

// 頁面按鈕
import Paginate from "vuejs-paginate-next";

export default {
  name: 'conpetitor',
  components: {
    NavBar2,
    paginate: Paginate,
  }, data() {

    return {
      page: '',
      Packages: [],
    }
  }, methods: {
    // 頁面按鈕：更換頁面重新取得下一頁資料
    clickCallback(pageNum) {
      const route = useRoute();
      let { keyword, address, store_name, county, district,
        floor, structure, age, min_floor_space, max_floor_space, min_total_floor_space, max_total_floor_space, min_price, max_price, date_range, type, status, d_type
      } = route.query;
      let url = `http://21centuryedm.ddns.net/api/estate-century/21century?page=${pageNum}&keyword=${keyword}&address=${address}&store_name=${store_name}&county=${county}&district=${district}&floor=${floor}&structure=${structure}&age=${age}&min_floor_space=${min_floor_space}&max_floor_space=${max_floor_space}&min_total_floor_space=${min_total_floor_space}&max_total_floor_space=${max_total_floor_space}&min_price=${min_price}&max_price=${max_price}&date_range=${date_range}&type=${type}&status=${status}&d_type=${d_type}`
      let token = sessionStorage.getItem('token');
      fetch(url, {
        headers: {
          Authorization: `Bearer ${token}`,
          token: token
        }
      }).then(res => {
        return res.json();
      }).then(formData => {
        this.Packages = formData.data
        console.log('===測試===');
        console.log(this.Packages);
      })
    }
  }, mounted() {
    $(document).ready(function () {
      // 按鈕文字顏色
      $('.pagination').attr('class', 'pagination mb-0');
      $('.page-link').css('border', 'transparent');
      $('.page-link').css('color', 'black');
      $('li.page-item').css('color', 'black');
      // $('.page-item.active .page-link').css('border', 'transparent')

      // 按下一頁後的樣式
      $('.page-item').css('background-color', 'transparent');

    });
  }, watch: {
    sortByOption: {
      handler(value) {
        this.sortByAge()
      },
      deep: true
    },

    // 頁面返回
    page: (OldVal, NewVal) => {
      console.log(OldVal, NewVal, '頁面切換,返回最上頁');
      window.scrollTo(0, 0);
    }

  }, created() {
    // 搜尋欄位的值
    const route = useRoute();
    let { age, brand, county, date_range, district, floor, keyword, max_floor_space, max_price, max_total_floor_space, min_floor_space, min_price, min_total_floor_space, status, structure, type
    } = route.query;

    // 獲取資料
    let url = `http://21centuryedm.ddns.net/api/estate-century/competitor?page=1&keyword=${keyword}&county=${county}&district=${district}&date_range=${date_range}&type=${type}&status=${status}&brand=${brand}&floor=${floor}&structure=${structure}&age=${age}&min_floor_space=${min_floor_space}&max_floor_space=${max_floor_space}&min_total_floor_space=${min_total_floor_space}&max_total_floor_space=${max_total_floor_space}&min_price=${min_price}&max_price=${max_price}&brand=${brand}`
    let token = sessionStorage.getItem('token');

    // Fetch取得資料
    fetch(url, {
      headers: {
        Authorization: `Bearer ${token}`,
        token: token
      }
    }).then(res => {
      return res.json();
    }).then(formData => {
      this.Packages = formData.data
    })
  }
}


</script>
